import React, { Component } from 'react';
import './TodoApp.css'
class TodoApp extends Component{
    constructor(){
        super()
        this.state={
            value:'',
            items:[]
        }
    }
    handleSubmit=(event)=>
    {
        event.preventDefault();
        const {value}=this.state;
        // const allItems=this.state.items;
        // allItems.push(value);
        this.setState(
            {
              items:[...this.state.items,value],
               value:''
            }
        )
        
    }
    handleChange=(event)=>
    {

        this.setState({
            value:event.target.value
        })
      
    }
    deleteClick=(index)=>
    {
        const {items}=this.state;
        items.splice(index,1);
        this.setState(
            {
                Items:this.state.items
            }
        )
        
    }
    clickEdit=()=>{
        
        

    }
    render(){
        const {value,items} = this.state;
        return(
            <div className="todo-container">
                <form className="input-container" onSubmit={this.handleSubmit}>
                    <h1>TodoApp</h1>
                    <input type="text" value={value} placeholder="Enter the inputs" onChange={this.handleChange}></input>
                </form>
                <ul>
                    {
                        
                      items.map((data,index)=>(
                          <li key={index}><input type="checkbox"></input>{data}
                          <div className="image-container">
                              <i id="edit" className="far fa-edit" onClick={()=>this.clickEdit(index)}></i>
                                 <i className="fas fa-trash-alt" onClick={()=>this.deleteClick(index)}>
                              </i></div></li>
                      ))
                    }
                </ul>

            </div>
     
        )
    }
}
export default TodoApp;